.content{
  padding: 32px;
}
.header{
  margin-bottom: 32px;
  h1{
    font: normal normal bold 24px/33px Lato;
  }
  a, a:hover{
    text-decoration: none;
    font: normal normal bold 16px/24px Lato;
    color: #000;
  }
  .addNewAdmin {
    justify-self: flex-start;
  }
  .addNewAdmin .material-icons{
    color: $darkgreen;
  }
  .search{
    margin-right: 26px;
  }
}

.table{
  margin-bottom: 63px !important;
  thead{
    tr{
      border-bottom: 2px solid #000;
      font: normal normal bold 16px/24px Lato;
    }
    .material-icons{
      color: $darkgreen;
    }
  }
  tr{
    border-bottom: 2px solid $lightgreen;
  }
}
.icon{
  color: #000;
}
.icon :hover{
  color: $darkgreen;
}
.pagination{
  .page-link{
    color: #000;
    border-color: $darkgreen;
  }
  .icon{
    color: $darkgreen;
  }
  .page-link:hover{
    color: #fff;
    background-color: $darkgreen;
  }
}
